.mapsComponent{overflow-x:hidden}.mapsComponent__city-name{position:absolute;display:inline-block;width:100vw;padding-top:20px;font-size:28px;font-weight:bold;color:#00FFD8;text-transform:uppercase;z-index:10;text-align:center}.mapsComponent__arrows__arrow-left,.mapsComponent__arrows__arrow-right{z-index:10;position:absolute;top:30px;padding:25px;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.mapsComponent__arrows__arrow-right{right:0}.mapsComponent__maps{width:300vw;display:-webkit-box;display:-ms-flexbox;display:flex}.mapsComponent__maps__map{width:100vw;height:100vh}.mapsComponent__maps__map:nth-child(1){background:url("../../static/img/mapsComponent/lyon.jpg")}.mapsComponent__maps__map:nth-child(2){background:url("../../static/img/mapsComponent/paris.jpg")}.mapsComponent__maps__map:nth-child(3){background:url("../../static/img/mapsComponent/marseille.jpg")}



