.splashscreenComponent{background:url("../../static/img/metro_bg.png") no-repeat;background-size:cover}.splashscreenComponent__logo{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.splashscreenComponent__loader{position:absolute;top:75%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:rgba(0,255,216,0.2);height:2px;width:50%;overflow:hidden}.splashscreenComponent__loader__loaded{height:inherit;width:0;background-color:#00FFD8}.splashscreenComponent__title,.splashscreenComponent__subtitle{color:#00FFD8;text-transform:uppercase}.splashscreenComponent__title{position:absolute;top:75%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:40px;white-space:nowrap;font-weight:bold}.splashscreenComponent__subtitle{position:absolute;top:80%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);white-space:nowrap;font-size:20px}



