html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*,*:before,*:after{box-sizing:border-box;-webkit-transform-origin:50% 50%;transform-origin:50% 50%}a,a:visited,a:link,a:hover,a:active{text-decoration:none;color:inherit}ul{list-style:none}@font-face{font-family:'Source Sans Pro';src:url("../static/fonts/SourceSansPro-Regular.ttf");font-weight:normal;font-style:normal}@font-face{font-family:'Source Sans Pro';src:url("../static/fonts/SourceSansPro-Semibold.ttf");font-weight:bold;font-style:normal}body{font-family:'Source Sans Pro', sans-serif}.screen{position:relative;width:100vw;height:100vh}



