.social{background:url("../../static/img/metro_bg.png") no-repeat;background-size:cover}.social__infos{position:relative;display:block;height:30vh}.social__message{position:absolute;top:60%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:inline-block;width:70%;color:#00FFD8;font-size:20px;text-align:center;text-transform:uppercase}.social__separator{position:absolute;top:75%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:40%;height:2px;background-color:#00FFD8}.social__items{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:distribute;justify-content:space-around;height:60vh}.social__item{-ms-flex-item-align:center;align-self:center}.social__item__img{display:block;margin:0 auto}.social__item__title{color:#00FFD8;text-align:center;font-size:15px;padding-top:15px}



